body {
    margin: 0;
    padding: 16px;
    font-family: Arial, Helvetica, sans-serif;
    ul {
        padding: 0;
        margin: 0;
        li {
            list-style: none;
        }
    }
    a {
        text-decoration: none;
    }
    h1, h2, h3, h4, h5, h6, p {
        margin: 0;
    }
}
@media(min-width: 720px) {
    body {
        padding: 0;
        max-width: 600px;
        margin: auto;
    }
}